import { useCallback } from 'preact/hooks';
import { SearchInput } from './search-input';
import { h } from 'preact';
export function Form(props) {
    var onKeyDown = useCallback(function (evt) {
        if (evt.code === 'ArrowUp') {
            evt.preventDefault();
            if (props.index > 0)
                props.up();
        }
        else if (evt.code === 'ArrowDown') {
            evt.preventDefault();
            if (props.index < props.resultsLength - 1)
                props.down();
        }
    }, [props.index, props.up, props.down, props.resultsLength]);
    return (h("form", { id: "search-form", onSubmit: props.onSubmit },
        h(SearchInput, { value: props.query, onInput: function (evt) { return props.onChange(evt.target.value); }, onKeyDown: onKeyDown })));
}
